import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
import './sdk'

Vue.use(Vuex)

export default new Vuex.Store({
  state: {
    lineId: ''
  },
  actions: {
    getLineId(context) {
      liff.init(
        data => {
          var lineId = data.context.userId
        },
        err => {}
      )
      context.commit('SETLINEID', lineId)
    }
  },
  mutations: {
    SETLINEID(state, lineId) {
      state.lineId = lineId
    }
  }
})
